.spinner1 {
    height: 12px;
    width: 12px;
    margin-bottom: -3px;
    animation: rotate 0.8s infinite linear;
    border-width: 4px;
    border-style: solid;
    border-radius: 50%;
    display: inline-block;
}

@keyframes rotate {
    0% {
        transform: rotate(0);
        border-color: #2e3192;
        border-right-color: transparent;
    }

    50% {
        transform: rotate(180deg);
        border-color: #2e3192;
        border-right-color: transparent;
    }

    100% {
        transform: rotate(360deg);
        border-color: #cb2025;
        border-right-color: transparent;
    }
}

